body {
  background-color: #26272c !important;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  font-family: "PT Serif";
}
.btn {
  border-radius: 0 !important;
  font-size: 80% !important;
}
.btn-success {
  background-color: #049747 !important;
}
.btn-outline-secondary {
  border: 0.5px solid rgba(255, 255, 255, 0.1) !important;
  color: #fff !important;
} /* ------------------------------------
            animation-element
--------------------------------------*/
.slide-from-top {
  transform: translateY(-50%);
  opacity: 0;
  transition: all 0.2s 0.8s ease-in;
}
.slide-from-top.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-3s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.2s 0.3s ease-in;
}
.slide-from-bottom-3s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-5s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.2s 0.5s ease-in;
}
.slide-from-bottom-5s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.fade-in-3s {
  opacity: 0;
  transition: all 0.2s 0.3s ease-in;
}
.fade-in-3s.in-view {
  opacity: 1;
}
.fade-in-8s {
  opacity: 0;
  transition: all 0.2s 0.8s ease-in;
}
.fade-in-8s.in-view {
  opacity: 1;
}
.zoom-out {
  transform: scale(3, 3);
  transition: all 0.5s ease-in;
}
.zoom-out.in-view {
  transform: scale(1, 1);
}
/* ------------------------------------
                NavBar
--------------------------------------*/

.navbar .navbar-brand {
  color: #fff;
}
.navbar .nav-link {
  color: #a0a0a0;
}
.navbar .nav-item.active .nav-link {
  color: #fff;
}
.navbar button.btn-success {
  padding: 10px 35px;
}
.navbar i.bx-cart {
  font-size: 24px;
}
.navbar i.bx-cart:hover {
  cursor: pointer;
}
/* ------------------------------------
                Section1
--------------------------------------*/
.section1-bg {
  width: 100%;
  position: absolute;
  background-image: linear-gradient(
      90deg,
      rgba(34, 34, 39, 1) 16%,
      rgba(255, 255, 255, 0.123) 100%
    ),
    url("./image/section1-bg.jpg");
  background-size: cover;
  background-position: right center;
}
#section1 a.btn-outline-secondary {
  font-size: 11px !important;
}
#section1 .triangle-bottom-right {
  background: linear-gradient(to bottom right, transparent 50%, #26272c 50.5%)
      no-repeat bottom,
    /* bottom part */ linear-gradient(0deg, transparent, transparent) no-repeat
      top;
  height: 200px;
}
#section1 .triangle-bottom-right a:hover {
  cursor: pointer;
}
#section1 .triangle-bottom-right i {
  font-size: 24px;
  padding: 0.5rem;
  border: 0.5px solid rgba(255, 255, 255, 0.1);
}
#section1 .triangle-bottom-right a:hover i {
  cursor: pointer;
  animation: example 0.4s;
}
@keyframes example {
  0% {
    padding-top: 0.4rem;
    padding-bottom: 0.6rem;
  }
  100% {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  50% {
    padding-top: 0.8rem;
    padding-bottom: 0.2rem;
  }
}
